.tb_c_r_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: auto;
}

.tb_c_r_post_slider{
    width: 100%;
    overflow: hidden;
}

.tb_c_r_post_wrapper{
    width: 100%;
    padding: 30px 10px;
    overflow: hidden;
}

.tb_c_r_fixed_height .tb_c_r_post_wrapper, 
.tb_c_r_fixed_height .tb_c_r_post_in{
    height: 100%;
}

.tb_c_r_post_in {
    display: flex;
    flex-direction: column;
    position: relative;
    border-radius: var(--tb-border-radius);
    overflow: hidden;
    cursor: pointer;
    transition: box-shadow .3s ease-in, transform .3s ease-in;
}

.tb_c_r_post_media_wrapp{
    position: relative;
    display: block;
}
.tb_c_r_image{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    transition: transform 3.525s .01s ease-out, opacity 3.525s ease-out;
}

.tb_c_r_image_wrap_, .tb_c_r_video_wrap_{
    padding-bottom: 178%;
    position: relative;
    background-color: rgba(0,0,0,.03);
}

.tb_wall_container .tb_c_r_image_wrap_, 
.tb_wall_container .tb_c_r_video_wrap_{
    padding-bottom: calc(var(--tb-theme-height) - var(--tb-padding));
}
.tb_c_r_image, .tb_c_r_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_c_r_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_c_r_post_media_wrapp .fb_iframe_widget_fluid{
    width: 100%;
    display: flex;
    align-items: center;
}

.tb_c_r_video video::-webkit-media-controls-panel {
    background-image: linear-gradient(transparent, transparent) !important;
}

.tb_c_r_post_in:hover .tb__media_ico_ .tb__icon,
.tb_c_r_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}
.tb_c_r_post_media_wrapp{
    position: relative;
}



.tb_c_r_arrow {
    position: absolute;
    top: calc(50% - 20px);
    background-color: #fff;
    border-radius: 3px;
    height: 40px;
    width: 40px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: #000;
    font-size: 16px;
    opacity: 0.75;
    z-index: 9;
    cursor: pointer;
    -webkit-transform: translateY(0) !important;
    transform: translateY(0) !important;
}

.tb_c_r_arrow:hover {
    opacity: 1;
}

.tb_c_r_arrow_left__ {
    left: 5px;
    right: auto;
    box-shadow: 3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_c_r_arrow_right__ {
    right: 5px;
    left: auto;
    box-shadow: -3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_a_spinner.tb_c_r_arrow_right__:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 18px;
    height: 18px;
    margin-top: -9px;
    border: 1.5px solid #000;
    border-right: 1.5px solid transparent;
    left: auto;
    right: 12px;
    display: flex;
    -webkit-animation: tb_btn_animation-spinner 0.5s linear infinite;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}